.btn {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 14px 24px;
  margin: 8px 0px;

  position: static;
  width: 356px;
  border-radius: 55px;

  color: #1A1A1A;  
  font-family: Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;

  cursor: pointer;
}

.yellow {
  background: #FFE44D;
  border: none;
}

.white {
  background: white;
  border: 0.5px solid #CFD8DC;
}
