.header {
	width: 100%;
	background-color: #ffe44d;
	padding: 12px 0;
}

.header__wrapper {
	display: flex;
	align-items: center;
	justify-content: space-between;
	gap: 80px;
}

@media (max-width: 700px) {
	.header__wrapper {
		flex-direction: column;
		align-items: stretch;
		gap: 20px;
	}
}


.iconsMenu {
	display: flex;
	gap: 30px;
	flex-wrap: wrap;
}

.favoritesLink {
	position: relative;
	width: 34px;
	height: 34px;
	margin-left: 10px;
}

.iconsMenu svg {
	width: 100%;
	height: 100%;
	/* min-width: ; */
}

.iconBubble {
	background-color: #01a54e;
	border-radius: 50%;
	color: #ffffff;
	border: 2px solid #FFE44D;
	font-size: 14px;
	width: 22px;
	height: 22px;
	line-height: 1.25;
	display: flex;
	align-items: center;
	justify-content: center;
	box-sizing: border-box;
	position: absolute;
	bottom: 60%;
	left: 70%;
}

.iconsMenuItem {
	display: flex;
	font-size: 18px;
	font-weight: 600;
	align-items: center;
	gap: 10px;
	text-decoration: none;
	color: #1A1A1A;
}

.iconsMenuItem svg {
	width: 28px;
	height: 28px;
	flex-shrink: 0;
	fill: currentColor;
}