/* @import "~normalize.css"; */

* {
	box-sizing: inherit;
}
html {
	box-sizing: border-box;
}
body {
	font-family: "Nunito", "Arial", "Helvetica", sans-serif;
	padding: 0;
	margin: 0;
	
}

.container {
	max-width: 1024px;
	padding-left: 16px;
	padding-right: 16px;
	width: 100%;
	margin: 0 auto;
}

.container_inner{
	margin-top:20px;
}

img {
	max-width: 100%;
}

.content {
	padding: 0 0 20px;
	flex:1;
}

.content__sort {
	margin-bottom: 40px;
}

#root {
	display: flex;
	flex-direction: column;
	/* justify-content: space-between; */
	min-height: 100vh;
}


